.tabs {
  position: fixed;
  display: flex;
  justify-content: space-around;
  bottom: 0;
  width: 100vw;
  box-shadow: 0px -4px 8px rgba(0, 0, 0, 0.1);
  background-color: #fff;
}
.tabs__item {
  padding: 8px 0 5px;
  display: inline-block;
  text-align: center;
}
.tabs__active {
  background-color: #d84741;
  height: 24px;
  width: 24px;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.tabs__img {
  display: inline-block;
  height: 22px;
  width: 22px;
}
.tabs__img--active {
  height: 18px;
  width: 18px;
}
.tabs__title {
  padding-top: 6px;
  font-size: 10px;
  line-height: 12px;
  color: #cccccc;
  font-family: PingFangSC-Regular;
}
.tabs__title--active {
  color: #d84741;
}
.top {
  position: fixed;
  display: flex;
  justify-content: center;
  height: 50px;
  width: 100%;
  background-color: #d84741;
  z-index: 1;
}
.top__left {
  position: absolute;
  top: 15px;
  left: 5px;
}
.top__container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
  color: #fff;
}
.top__text {
  line-height: 50px;
  text-align: center;
  font-size: 18px;
  font-family: PingFangSC-Medium;
}
.top__search input {
  height: 30px;
  width: 70vw;
  border-radius: 15px;
  padding-left: 15px;
  border: none;
  outline: none;
  color: #333;
  background-color: #eeeeee;
}
.top__right {
  position: absolute;
  top: 17px;
  right: 5px;
}
.top__slideimg {
  height: 20px;
  width: 20px;
}
.songlist {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.songlist__item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30.666vw;
  margin-bottom: 15px;
}
.songlist__item .img {
  height: 105px;
  width: 105px;
  border-radius: 5px;
}
.songlist__item .title {
  display: -webkit-box;
  width: 105px;
  font-size: 12px;
  line-height: 16px;
  overflow: hidden;
  color: #333;
  font-family: PingFangSC-Light;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  /* autoprefixer: off     使用这个浏览器才能识别下列方法*/
  -webkit-box-orient: vertical;
  /* autoprefixer: on*/
}
.songlist__item .fixed {
  position: absolute;
  right: 10px;
  top: 0;
}
.songlist__item .fixed__img {
  height: 14px;
  width: 14px;
  vertical-align: middle;
}
.songlist__item .fixed__number {
  font-size: 12px;
  color: #fff;
}
.ranklist__line {
  display: flex;
  flex-direction: column;
}
.ranklist__line .line__item {
  position: relative;
  display: flex;
  flex-direction: row;
  padding: 10px 0 5px;
}
.ranklist__line .line__img {
  height: 80px;
  width: 80px;
  border-radius: 5px;
}
.ranklist__line .line__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 10px;
}
.ranklist__line .line__content .content span {
  font-size: 14px;
  color: #666666;
  line-height: 20px;
}
.ranklist__line .line__fixed {
  position: absolute;
  bottom: 10px;
  left: 5px;
  font-size: 10px;
  color: #fff;
  font-family: PingFangSC-Light;
}
.ranklist__block {
  display: flex;
  flex-wrap: wrap;
  padding-top: 15px;
}
.ranklist__block .block__img {
  position: relative;
  height: 27vw;
  width: 27vw;
  padding: 0 1.833vw 4vw;
}
.ranklist__block .block__img img {
  height: 100%;
  width: 100%;
  border-radius: 5px;
}
.ranklist__block .block__fixed {
  position: absolute;
  bottom: 20px;
  left: 10px;
  font-size: 10px;
  color: #fff;
  font-family: PingFangSC-Light;
}
.home {
  position: relative;
  margin-bottom: 55px;
  overflow: scroll;
  background-color: #fff;
}
.home__banner {
  width: 90vw;
  margin: 65px auto 15px;
  border-radius: 15px;
}
.home__banner .carousel .slider-wrapper {
  border-radius: 5px;
}
.home__icons {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 100vw;
  padding-bottom: 15px;
  border-bottom: 0.5px solid #eee;
}
.home__icons .icon__item .item__img {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 45px;
  width: 45px;
  margin: 0 auto;
  border-radius: 22.5px;
  background-color: #d84741;
}
.home__icons .icon__item .item__img img {
  height: 22px;
  width: 22px;
}
.home__icons .icon__item .item__title {
  padding-top: 10px;
  color: #999;
  font-size: 12px;
  text-align: center;
}
.home__personalized {
  padding: 0 15px;
}
.home__personalized .personalized__header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 50px;
}
.home__personalized .personalized__header .title {
  font-size: 20px;
  font-family: PingFangSC-Medium;
}
.home__personalized .personalized__header .redirect {
  padding: 4px 10px;
  border: 1px solid #999;
  border-radius: 15px;
  font-size: 14px;
  color: #666;
}
/* 排行榜 */
.rank {
  margin-bottom: 55px;
  overflow: scroll;
  background-color: #fff;
}
.rank__container {
  padding: 55px 4vw 0;
}
.rank__content {
  padding-top: 15px;
}
.rank__title {
  color: #333333;
  font-size: 18px;
  font-family: PingFangSC-Medium;
  font-weight: 500;
}
html,
body {
  height: 100%;
  background: #f4f4f4;
  -webkit-overflow-scrolling: touch;
}
.App,
#root {
  height: 100%;
  background: #f4f4f4;
}
a {
  text-decoration: none;
  color: #000;
}
body {
  font-family: PingFangSC-Regular;
  margin: 0;
  padding: 0;
}
button {
  outline: none;
  border: none;
  padding: 0;
  background: none;
}
input,
button,
span,
a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
